import React from "react";

function Privacy() {
  return (
    <div  className="pnt" >
      <h1>Aloy Privacy Policy</h1>

      <p>Last Updated: 25th July 2023</p>

      <p>
        Welcome to Aloy! This Privacy Policy outlines how we collect, use,
        disclose, and protect your personal information when you use our mobile
        application ("App"). By using Aloy, you agree to the terms of this
        Privacy Policy.
      </p>

      <h2>Information We Collect</h2>
      <p>
        When you use Aloy, we may collect the following types of information:
      </p>
      <ul>
        <li>
          Your name, email address, phone number, and other contact information
          you provide us.
        </li>
        <li>
          Information about businesses you interact with through the App,
          including loyalty points earned and redeemed.
        </li>
        <li>
          Usage information about how you interact with the App, including
          device information, log data, and browsing activity.
        </li>
        <li>
          Location information if you grant us permission to do so, which we use
          to offer location-based features.
        </li>
      </ul>

      <h2>How We Use Your Information</h2>
      <p>We may use your information for the following purposes:</p>
      <ul>
        <li>
          Managing and facilitating loyalty points for businesses you engage
          with.
        </li>
        <li>Providing customer support and responding to your inquiries.</li>
        <li>
          Sending you updates, offers, and promotional materials related to
          businesses and our App.
        </li>
        <li>Improving and personalizing your experience with the App.</li>
        <li>Conducting data analysis and research to enhance our services.</li>
      </ul>

      <h2>Sharing Your Information</h2>
      <p>We may share your information in the following circumstances:</p>
      <ul>
        <li>
          With businesses you interact with through the App to manage and track
          loyalty points.
        </li>
        <li>
          With service providers who assist us in operating and improving our
          App.
        </li>
        <li>
          If required by law or to protect our rights, safety, or property, or
          the rights, safety, or property of others.
        </li>
        <li>
          In connection with a business transaction, such as a merger,
          acquisition, or sale of assets.
        </li>
      </ul>

      <h2>Data Deletion</h2>
      <p>
        If you wish to delete your personal information from our App, you can
        make a request by contacting us at the email address provided below. We
        will promptly respond to your request and take reasonable steps to
        delete your data from our records, except where we are required to
        retain certain information for legal or legitimate business purposes.
      </p>

      <h2>Security</h2>
      <p>
        We take the security of your information seriously and employ reasonable
        measures to protect it. However, please note that no data transmission
        or storage can be guaranteed to be 100% secure. If you have any concerns
        about the security of your data, please contact us using the information
        provided below.
      </p>

      <h2>Your Choices</h2>
      <p>
        You may have certain rights and choices regarding your personal
        information, including the ability to update, correct, or delete it. You
        can also opt-out of receiving marketing communications from us.
      </p>

      <h2>Contact Us</h2>
      <p>
        If you have any questions, concerns, or requests related to this Privacy
        Policy or your personal information, please contact us at:
      </p>
      <p>Email: hello@getaloy.com</p>
      <p>Phone: 91-901-962-7362</p>

      <p>
        Please note that this Privacy Policy may be updated from time to time.
        You can always find the latest version on this page.
      </p>
    </div>
  );
}

export default Privacy;
